/* src/app/components/chat/chat.component.css */
.chat-layout {
  display: flex;
  height: calc(100vh - 65px);
  font-family:
    Arial,
    "Liberation Sans",
    "Segoe UI",
    "Roboto",
    "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f4f6fb;
  color: #222;
}
.side-panel {
  width: 320px;
  background: #f8f8f8;
  border-right: 1px solid #e0e0e0;
  padding: 16px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.side-panel-header {
  width: 100%;
  margin-bottom: 20px;
}
.model-details {
  text-align: center;
  margin-top: 16px;
}
.model-logo {
  width: 64px;
  height: 64px;
  object-fit: contain;
  margin-bottom: 8px;
}
.model-name {
  font-weight: 600;
  font-size: 1.13em;
  letter-spacing: 0.02em;
}
.model-company {
  color: #888;
  font-size: 0.97em;
  margin-top: 4px;
  font-style: italic;
}
.model-description {
  font-size: 0.8em;
  text-align: left;
  margin-top: 20px;
  line-height: 1.4;
}
.model-link {
  text-align: left;
}
.chat-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 24px;
  box-sizing: border-box;
  overflow: hidden;
}
.header {
  padding: 10px;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ddd;
}
.input-bar {
  display: flex;
  padding: 10px;
  background-color: #fff;
  border-top: 1px solid #ddd;
}
.input-bar input {
  flex: 1;
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-family: inherit;
  font-size: 1em;
}
.input-bar button {
  padding: 10px 15px;
  background-color: #007bff;
  border: none;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  font-family: inherit;
  font-size: 1em;
  font-weight: 500;
  letter-spacing: 0.02em;
}
.input-bar button:hover {
  background-color: #0056b3;
}
.loading {
  text-align: center;
  font-style: italic;
  margin: 10px 0;
}
.error {
  color: red;
  text-align: center;
  margin: 10px 0;
}
.no-messages {
  font-style: italic;
  color: #aaa;
  font-size: 1em;
  text-align: center;
  margin-top: 20px;
  font-family:
    Arial,
    "Liberation Sans",
    "Segoe UI",
    "Roboto",
    "Helvetica Neue",
    sans-serif;
}
.chat-layout,
.chat-history,
.input-bar input,
.input-bar button,
.model-name,
.model-company,
.model-logo,
.no-messages {
  font-family:
    "Open Sans",
    Arial,
    "Liberation Sans",
    "Segoe UI",
    "Roboto",
    "Helvetica Neue",
    sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/*# sourceMappingURL=chat.component.css.map */
